import React from "react";
import { Row, Col } from "react-flexbox-grid";
import "./about.scss";
// Components
import TeamBox from "./teamBox";
import TeamInfo from "./teamInfo";
import Title from "../ui-components/title/title";
// Assets
import Person01 from "../../assets/about/About.png";
import Person02 from "../../assets/about/About2.png";

const about = () => (
  <div id="about">
    <div className="wrapper">
      <Title title="ABOUT US." />
      <p className="font12">
        Established in 2021, Devine Travel and Tours operates between
        <br></br>Gauteng and Limpopo, specializing in daily shuttle services.
      </p>
      <Row>
        <Col md={12} lg={4}>
          <TeamBox
            avatar={Person01}
            name="Mercedes Benz Sprinter"
            job="22 Seater"
          />
        </Col>
        <Col md={12} lg={4}>
          <TeamBox avatar={Person02} name="Toyota Avanza" job="7 Seater" />
        </Col>
        <Col md={12} lg={4}>
          <TeamInfo />
        </Col>
      </Row>
    </div>
  </div>
);

export default about;
