import React from "react";
import { Row, Col } from "react-flexbox-grid";
// SCSS
import "./hero.scss";
//Assets
import HeroImage from "../../assets/hero/hero.png";
import HeroBackground from "../../assets/hero/background.jpg";
//Components
import Button from "../ui-components/button/button";

const Hero = () => (
  <div
    className="hero"
    id="hero"
    style={{ backgroundImage: `url(${HeroBackground})` }}
  >
    <div className="hero-overlay"></div>
    <div className="wrapper">
      <Row>
        <Col md={12} lg={6}>
          <div className="hero-info">
            <h1 className="weight800 font60">THE NEXT LEVEL IN LOGISTICS.</h1>
            <p className="font12">
              Experience the future of transportation with our advanced
              logistics solutions. We offer seamless, reliable, and efficient
              travel between locations, ensuring comfort and safety every step
              of the way.
            </p>
            <Button label="GET A QUOTE" target={"contact"} />
          </div>
        </Col>
        <Col md={12} lg={6}>
          <div className="hero-image">
            <img src={HeroImage} alt="hero" />
          </div>
        </Col>
      </Row>
    </div>
  </div>
);

export default Hero;
