import React from "react";
import { Row, Col } from "react-flexbox-grid";
import Masonry from "react-masonry-css";
// Scss
import "./portfolio.scss";
// Assets
import Arrow from "../../assets/portfolio/arrow.svg";
import Preview1 from "../../assets/portfolio/project01/preview.png";
import Preview2 from "../../assets/portfolio/project02/preview.png";
import Preview3 from "../../assets/portfolio/project03/preview.png";
import Preview4 from "../../assets/portfolio/project04/preview.png";
import Preview5 from "../../assets/portfolio/project05/preview.png";
import Preview6 from "../../assets/portfolio/project06/preview.png";
// Components
import Button from "../ui-components/button/button";
import Title from "../ui-components/title/title";
import ProjectBox from "../ui-components/projectBox/projectBox";

class Portfolio extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      // PORTFOLIO PROJECTS
      projects: [
        {
          id: "1",
          preview: Preview1,
          title: "TRAILERS",
          tag: "We have trailers, no worries on luggage",
        },
        {
          id: "2",
          preview: Preview2,
          title: "COURIER",
          tag: "We do same day courier services",
        },
        {
          id: "3",
          preview: Preview3,
          title: "SCHOLAR TRANSPORT",
          tag: "Kids transport is also available",
        },
        {
          id: "4",
          preview: Preview4,
          title: "INTERPROVINCIAL",
          tag: "Travel across South Africa",
        },
        {
          id: "5",
          preview: Preview5,
          title: "Private Booking",
          tag: "We do private bookings for private trips",
        },
        {
          id: "6",
          preview: Preview6,
          title: "VEHICLES",
          tag: "Variety of vehicles are available",
        },
      ],
    };
  }

  // RENDER
  render() {
    // PORTFOLIO GALLERY RENDER
    const projectsRender = this.state.projects.map((project) => (
      <ProjectBox
        preview={project.preview}
        key={project.id}
        title={project.title}
        tag={project.tag}
      />
    ));

    // PORTFOLIO GALLERY BREAKPOINTS
    const portfolioBreakpoints = {
      default: 3,
      1100: 3,
      700: 2,
      500: 1,
    };

    return (
      <div id="portfolio">
        <div className="wrapper">
          <Title title="SOME TRIPS." />
          <p className="font12">
            We travel all accross South Africa, ensuring that you are safe and
            <br></br>protected all the time with our premium services
          </p>
          <Masonry
            breakpointCols={portfolioBreakpoints}
            className="my-masonry-grid"
            columnClassName="mint__gallery"
          >
            {projectsRender}
          </Masonry>
          <Row className="flex-center padding40">
            <Button label="HAVE WORK FOR US?" target={"contact"} />
          </Row>
        </div>
      </div>
    );
  }
}

export default Portfolio;
