import React from "react";
import { Row, Col } from "react-flexbox-grid";
import "./contactInfo.scss";
import { MapPin, Phone, Mail } from "lucide-react"; // Import Lucide icons

import ContactInfoBox from "../contactInfo/contactInfoBox";

const contactInfo = () => (
  <Row>
    <Col xs={12}>
      <Row center="xs">
        <Col xs={12} lg={3} className="contact__info">
          <ContactInfoBox
            icon={<MapPin color="#160ca0" size={24} />}
            textLine1="Johannesburg, Pretoria"
            textLine2=""
          />
        </Col>
        <Col xs={12} lg={3} className="contact__info">
          <ContactInfoBox
            icon={<Phone color="#160ca0" size={24} />}
            textLine1="+27 71 539 9661"
            textLine2=""
          />
        </Col>
        <Col xs={12} lg={3} className="contact__info">
          <ContactInfoBox
            icon={<Mail color="#160ca0" size={24} />}
            textLine1="quote@divinetraveltours.co.za"
            textLine2=""
          />
        </Col>
      </Row>
    </Col>
  </Row>
);

export default contactInfo;
