import React from "react";
// SCSS
import "./teamInfo.scss";

const about = () => (
  <div className="team__info flex-center">
    <div>
      <h4 className="font20 weight800">Little About Us</h4>
      <p className="font12 weight500">
        We offer efficient and competitively priced transportation solutions
        available around the clock. Our commitment to fostering strong business
        relationships with clients is at the core of our service. In addition to
        shuttles, we provide courier deliveries, furniture removals, and
        vacation services, among other offerings.
      </p>
      {/* <p className="font12 weight500">Read More</p> */}
    </div>
  </div>
);

export default about;
